import React, { Component } from 'react'
import { withRouter } from 'react-router-dom';
import loadjs from 'loadjs';
import Parser from 'html-react-parser';
import OwlCarousel from 'react-owl-carousel';

export class Portfolio extends Component {

    componentWillMount() {
        loadjs('/js/main.js', () => { });
    }
    render() {
        return (
            <div>
                <section className={this.props.location.pathname != "/portfolio" ? "testimonial-area section-gap" : "testimonial-area section-gap gap"}>
                    <div className="container">
                        <div className="row d-flex justify-content-center">
                            <div className="menu-content pb-70 col-lg-8" style={{ paddingBottom: "20px" }}>
                                <div className="title text-center">
                                    <h1 className="mb-10 heading">Featured Projects</h1>
                                </div>
                            </div>
                        </div>

                        <div className="row">

                            <div className={this.props.location.pathname != "/portfolio" ?
                             "active-testimonial item pClass" : "profolioCls pClass"}>
                                {this.props.projects.length && (
                                    <OwlCarousel
                                        className="owl-theme"
                                        loop
                                        margin={10}
                                        items={2}
                                        infiniteLoop={true}

                                        
                                        nav
                                        navText={["<span class='lnr lnr-arrow-left'></span>", "<span class='lnr lnr-arrow-right'></span>"]}
                                        responsive={{
                                            0: {
                                                items: 1
                                            },
                                            480: {
                                                items: 1,
                                            },
                                            768: {
                                                items: 2,
                                            }
                                        }
                                        }
                                    >
                                        {this.props.projects
                                            .map((project, index) =>

                                                <div className="single-testimonial item d-flex flex-row" key={index}>
                                                    <div className="desc">
                                                        <h5>{project.title}</h5>
                                                        <p>
                                                            {Parser(project.description)}                                                  </p>
                                                    </div>
                                                    {project.tags
                                                        .map((tag, index) =>
                                                            <a key={index} className="genric-btn primary-border circle">{tag}</a>
                                                        )}
                                                </div>
                                            )}

                                        {/* <div className="single-testimonial item d-flex flex-row">
                                    <div className="desc">
                                    <h4>Diagnostic Portal</h4>
                                        <p>
                                            A purpose is the eternal condition for success. Every former smoker can tell you just how hard it is to stop smoking cigarettes. However.
                                        </p>
                                    </div>
                                </div> */}
                                    </OwlCarousel>
                                )}
                            </div>


                        </div>

                    </div>
                </section>
            </div >
        )
    }
}
export default withRouter(Portfolio);

// export default Portfolio
